import React from 'react'
import './index.css'
import prizes from '../src/prizes.json'

class Selection extends React.Component {

    createOptionElementsFromList( props ){
        return props.options.map( ( option, i ) => <option {...props}  key={i}>{option}</option> )
    }

    render(){
        return(
            <div className={"Selection "}>
                <select {...this.props}>
                    { this.createOptionElementsFromList( this.props ) };
                </select>
            </div>
        )
    }
}

class YearCategoryElement extends React.Component {
    constructor(props){
        super(props);
        this.year = props.year;
        this.category = props.category;
        this.onClick = props.onClick;
        this.laureates = props.laureates;
        this.overallMotivation = props.overallMotivation;

    }

    render(){

        return(
            <div className="YearCategoryElement" onClick={()=>this.onClick(this.props.laureates, this.overallMotivation)}>
                <div className="Year" >{this.year}</div>
                <div className='Category'>{this.category}</div>
            </div>
        )
    }
}

class ListOfYearCategoryElements extends React.Component {

    constructor(props){
        super(props);
        this.prizes = props.prizes;
        this.onClick = props.onClick;
        this.overallMativation = props.ovarallMatiovation;
    }

    render(){
        return(
           <div className="listOfYearCategoryElements">
               {this.prizes.map( d => <YearCategoryElement {...d} onClick={this.onClick}/>)}
           </div>
        )
    }
}

let ListOfLaureates = ( data ) => {

    return (
        <div>
            <div>
                {data.laureates.map( (d, i) => {
                    return(
                        <div className='laureat' key={i}>
                            <div className='name'>
                                <div className="firstname">{d.firstname}</div>
                                <div className="surname">{d.surname}</div>
                            </div>
                            <div className="motivation">{d.motivation}</div>
                        </div>
                    )
                })}
            </div>
        </div>
    )
};

class App extends React.Component {

    constructor(props){
        super(props);
        this.changeShowedInfo = () => this.setState()
        this.state = {

            prizes: prizes,
            laureates: prizes.prizes[0].laureates,
            overallMotivation: prizes.prizes[0].overallMotivation

        }
    }

    render(){

        return (
            <div className="App">
                <div className="header">

                </div>
                <div className="Laureates">

                    <ListOfYearCategoryElements {...this.state.prizes} onClick={(laureates, overallMotivation )=>this.setState({laureates: laureates, overallMotivation: overallMotivation})}/>

                    <div className="TableInfo">
                        <h1 className={'title'}>Лауреаты Нобелевской премии</h1>
                        <div>
                            <div className={'overallMotivation'}>{this.state.overallMotivation}</div>
                            <ListOfLaureates laureates={this.state.laureates}/>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default App
